import React, { forwardRef } from 'react';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';

import bill from '../assets/ejesProv/billetera.png';

const EjesModal = ({ open, handleClose }) => {
	const classes = useStyles();

	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			>
				<AppBar className={classes.appBar}>
					<Toolbar>
						<IconButton
							edge='start'
							color='inherit'
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Ejes De Campaña
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ padding: 40 }}>
					<ContentEjes />
				</div>
			</Dialog>
		</div>
	);
};

const ContentEjes = () => {
	return (
		<Grid container spacing={3}>
			{ejesC.map((eje) => (
				<Grid item xs={6}>
					<CardEjes title={eje.name} />
				</Grid>
			))}
		</Grid>
	);
};

const CardEjes = ({ title }) => {
	return (
		<Card>
			<CardActionArea>
				<CardMedia
					style={{ height: 140 }}
					image={bill}
					title='Contemplative Reptile'
				/>
				<CardContent>
					<Typography gutterBottom variant='h5' component='h2'>
						{title}
					</Typography>
					<Typography variant='body2' color='textSecondary' component='p'>
						Lizards are a widespread group of squamate reptiles, with over 6,000
						species, ranging across all continents except Antarctica
					</Typography>
				</CardContent>
			</CardActionArea>
			<CardActions>
				<Button size='small' color='primary'>
					Share
				</Button>
				<Button size='small' color='primary'>
					Learn More
				</Button>
			</CardActions>
		</Card>
	);
};

const ejesC = [
	{ name: 'Medio Ambiente', img: medam },
	{ name: 'Obras públicas' },
	{ name: 'Género' },
	{ name: 'Espacios Publicos y Barrios' },
];

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'relative',
	},

	title: {
		marginLeft: 5,
	},
}));

export default EjesModal;
